

<template>
<div id="app">


    <div>{{ year }}{{ month + 1 }}{{ day }} <span @click="toggle"></span></div>

    <div v-bind:class="{hide: isHide}">
        <header class="header">
            <span class="prev" @click="prevMon"></span>
            <span class="info">
                {{ year }}{{ month + 1 }}
            </span>
            <span class="next" @click="nextMon"></span>
        </header>
        <section class="main">
        <table>
            <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(dir,entry) in viewData">
                        
                    <td v-for="(index,value) in entry" @click="pick($event,index,dir)">
                        {{ value }}
                    </td>
                </tr>
           

  </tbody>
        </table>
        </section>

    </div>
   
</div>
</template>

<script>
 import 'babel-polyfill';
import Vue from 'vue';   

const calendarData = (year) => {
    //
    let allMonthsDay = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31]

    //29
    if((year % 4 === 0 && year % 100 !== 0) || year % 400 === 0) {
        allMonthsDay[1] = 29
    }

    // 
    let previousMonthDays = [].concat(allMonthsDay) //allMonths
    previousMonthDays.unshift(previousMonthDays.pop())

    //
    let beforeDays = new Array(12)
        .fill(null)  //???????????????
        .map((item, index) => {
            //1
            let day = new Date(year, index, 1).getDay()
            if(day === 0) {
                return 6
            } else {
                return day
            }
        })

    //
    return new Array(12)
        .fill([])
        .map((month, index) => {
            let currentDays = allMonthsDay[index],//
                addBefore = beforeDays[index],//
                addDays = previousMonthDays[index],//
                monthData = []
                //
                for(;addBefore>0; addBefore--){
                    monthData.unshift(addDays--)
                }
                //
                for(let i=0;i<currentDays;){
                    monthData.push(++i)
                }
                //
                for(let i=42-monthData.length,j=0;j<i;){
                    monthData.push(++j)
                }
                return monthData
        })
}

const now =  new Date()
const year = now.getFullYear()
export default {
    data(){
        return{
            year: year,
            month: now.getMonth(),
            day: now.getDate(),
            isHide: false
        }
    },
    computed: {
        allYearData: function () {
            return calendarData(this.year)
        },
        viewData: function () {
            let month = calendarData(this.year)[this.month],
                row = []
            month.forEach((day, index) => {

                if(index%7 === 0) {
                    row.push(month.slice(index, index+7))
                }
            })
            return row
        },
        fff: function(){
            return 'ffff'
        }
    },
    methods: {
    nextMon: function(){
            if (this.month === 11) {
                ++this.year
                this.month = 0 
            } else {
                ++this.month
            }
        },
        prevMon: function(){
            if (this.month === 0) {
                --this.year
                this.month = 11
            } else {
                --this.month
            }
        },

        pick: function(event,index,dir){
            
            
                        let month = calendarData(this.year)[this.month],
                
                //
                allDaysOfMonth = (() => {
                    let i = month.indexOf(1),
                        j = month.indexOf(1, i+1),
                        arr = new Array(42)
                    arr.fill('prevMon', 0, i)
                    arr.fill('thisMon', i, j)
                    arr.fill('nextMon', j)
                    return arr
                })();
                
                let type = allDaysOfMonth[index+7*dir]
            switch(type){

                case 'prevMon':
                    this.prevMon()
                    break
                case 'thisMon':
                    this.day = event.target.innerText
                    break
                case 'nextMon':
                    this.nextMon()
                    break
            }
            

        },

        toggle: function(e){
            
            this.isHide ? e.target.innerText = '' : e.target.innerText = ''
            this.isHide = !this.isHide
        }    
    }
}

</script>




